<template>
  <section>
    <div>
      <h5 class="text-primary">{{ className }}</h5>
    </div>
    <SelectClass @getClassData="changeStatus" v-if="status" />
    <Choose v-else />
  </section>
</template>

<script>
import SelectClass from '@/components/topic/SelectClass.vue';
import Choose from '@/components/topic/ChooseModel.vue';

export default {
  data() {
    return {
      status: true,
      className: '',
    };
  },
  methods: {
    changeStatus(id) {
      this.status = !this.status;
      this.className = id;
    },
  },
  components: { SelectClass, Choose },
};
</script>
